.swipeButtons {
    position: fixed;
    bottom: 3vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.swipeButtons .MuiButtonBase-root {
    background-color: white;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.5) !important;
    transition: transform 40ms;
}

.swipeButtons .MuiButtonBase-root:hover {
    transform: scale(1.15);
}

.swipeButtons__replay {
    padding: 3vw;
    color: #f5b748 !important;
}

.swipeButtons__close {
    padding: 3vw;
    color: #ec5e6f !important;
}

.swipeButtons__starRate {
    padding: 3vw;
    color: #62b4f9 !important;
}

.swipeButtons__favorite {
    padding: 3vw;
    color: #76e2b3 !important;
}

.swipeButtons__flashOn {
    padding: 3vw;
    color: #915dd1 !important;
}